<template lang="html">
  <section class="module-advices">
    <section class="module-advices-filter">
      <div class="module-advices-filter-seasons">
        <div class="module-advices-filter-seasons-d1">
          <span class="module-advices-filter-title">季度总结：</span>
        </div>
        <div class="module-advices-filter-seasons-d2">
            <span
              v-for="season in seasons"
              :key="season.id"
              class="module-advices-filter-content"
            >
              {{season.name}}
            </span>
        </div>
      </div>
      <div class="module-advices-filter-topics">
        <div class="module-advices-filter-topics-d1">
          <span class="module-advices-filter-title">话题分类：</span>
        </div>
        <div class="module-advices-filter-topics-d2">
          <span v-for="job in jobs" :key="job.id" class="module-advices-filter-content">
            {{job.name}}
          </span>
        </div>
      </div>
    </section>
    <section class="module-advices-main" v-for="advice in advices">
      <div class="module-advices-main-to">
        <span>提意见给：{{advice.to}}</span>
      </div>
      <div class="module-advices-main-title">
        <span class="module-advices-main-from">{{advice.from}}</span>
        <span class="module-advices-main-title-content">{{advice.title}}</span>
      </div>
      <div class="module-advices-main-content">
        <span>{{advice.content}}</span>
      </div>
      <div class="module-advices-main-comments">
        <div class="module-advices-main-commnets-button" @click="toggleComment(advice.id, $event)">
          <img :src="imgQipao"/>
          <span v-if="isOpen(advice.id)">收起评论</span>
          <span v-else>{{advice.comments.length}}条评论</span>
        </div>
        <div v-show="isOpen(advice.id)" class="module-advices-main-comments-contents">
          <div class="module-advices-main-comments-contents-title">
            <span>{{advice.comments.length}}条评论</span>
          </div>
          <div class="module-advices-main-comments-contents-c" v-for="comment in advice.comments">
            <div class="module-advices-main-comments-contents-c-from">
              <span class="module-advices-main-comments-contents-c-from-s1">{{comment.from}}</span>
              <span class="module-advices-main-comments-contents-c-from-s2">{{comment.title}}</span>
              <span class="module-advices-main-comments-contents-c-from-s3">{{comment.time}}</span>
            </div>
            <div class="module-advices-main-comments-contents-c-content">
              <span>{{comment.content}}</span>
            </div>
          </div>
        </div>
      </div>
    </section>
  </section>
</template>

<script>
import { seasons, jobs, advices } from '@service/analog-data'
import imgQipao from '@assets/imgs/qipao.png'
export default {
  name: 'module-advices',
  data () {
    return {
      imgQipao,
      commentsOpenList: []
    }
  },
  computed: {
    seasons () {
      return seasons
    },
    jobs () {
      return jobs
    },
    advices () {
      return advices
    }
  },
  methods: {
    isOpen (adviceId) {
      const index = this.commentsOpenList.findIndex(id => id === adviceId)
      return index > -1
    },
    hideComment (adviceId) {
      this.commentsOpenList = this.commentsOpenList.filter(id => id !== adviceId)
    },
    showComment (adviceId) {
      this.commentsOpenList = [...this.commentsOpenList, adviceId]
    },
    toggleComment (adviceId, event) {
      const target = event.target
      const tagName = target.tagName
      if (tagName !== 'SPAN' && tagName !== 'IMG') {
        return
      }
      const isOpen = this.isOpen(adviceId)
      if (isOpen) {
        this.hideComment(adviceId)
      } else {
        this.showComment(adviceId)
      }
    }
  }
}
</script>

<style lang="scss">
@import '../../../styles/variable.scss';
@import '../../../styles/mixin.scss';

.module-advices {
  font-family: $base-font-family;
}

.module-advices-filter {
  @include module-block;
  background-color: $module-bkg;
  font-size: 20px;
}

.module-advices-filter-title {
  color: #5f5f5f;
}

.module-advices-filter-content {
  color: #999999;
  font-family: $base-font-family2;
  margin-right: 10px;
  padding: 6px 15px;
  border: 1px solid #b4b4b4;
  border-radius: 20px;
  display: inline-block;
  @include pointer;
}

.module-advices-filter-content:hover, .module-advices-filter-content-active {
  $bk-color: #1cba5b;
  background-color: $bk-color;
  color: #fff;
  border-color: $bk-color;
}

.module-advices-filter-topics {
  $bottom: 4px;
  margin-top: 16px;
  @include flexAlignCenter;

  @at-root {
    .module-advices-filter-topics-d1 {
      padding-bottom: $bottom;
    }

    .module-advices-filter-topics-d2 {
      flex: 1;
      & span {
        margin-bottom: $bottom;
      }
    }
  }
}

.module-advices-main {
  margin-top: 20px;
  @include module-block(30px);
}

.module-advices-main-to {
  color: #8093ac;
  font-size: 20px;
}

.module-advices-main-title {
  margin-top: 24px;
  font-family: $base-font-family3;
  color: #272a2f;
  font-size: 24px;
}

.module-advices-main-title-content {
  margin-left: 14px;
  display: inline-block;
}

.module-advices-main-from {
  padding: 12px 8px;
  background-color: #585858;
  display: inline-block;
  border-radius: 8px;
  color: #fff;
  font-size: 18px;
}

.module-advices-main-content {
  margin-top: 24px;
  font-size: 18px;
  color: #272a2f;
}

.module-advices-main-comments {
  margin-top: 14px;
}

.module-advices-main-commnets-button {
  @include flexAlignCenter;
  & img {
    width: 17px;
    @include pointer;
  }

  & span {
    font-size: 18px;
    color: #7d8aa7;
    margin-left: 8px;
    @include pointer;
  }
}

.module-advices-filter-seasons {
  $bottom: 4px;
  @include flexAlignCenter;
  @at-root {
    .module-advices-filter-seasons-d1 {
      padding-bottom: $bottom
    }

    .module-advices-filter-seasons-d2 {
      flex: 1;
      & span {
        margin-bottom: $bottom;
      }
    }
  }
}



.module-advices-main-comments-contents {
  margin-top: 18px;
  border: 1px solid #dfdfdf;
  padding: 15px;
  border-radius: 4px;
}

.module-advices-main-comments-contents-title {
  font-size: 18px;
  color: #272a2f;
  padding-left: 6px;
}

.module-advices-main-comments-contents-c {
  margin-top: 18px;
  border-top: 1px solid #dfdfdf;
  padding: 18px 6px 0 6px;
}

.module-advices-main-comments-contents-c-from {
  & span {
    display: inline-block;
  }
}

.module-advices-main-comments-contents-c-from-s1 {
  padding: 10px 5px;
  color: #fff;
  background-color: #aaaaaa;
  border-radius: 8px;
}

.module-advices-main-comments-contents-c-from-s2 {
  font-size: 14px;
  color: #3a3c40;
  margin-left: 11px;
}

.module-advices-main-comments-contents-c-from-s3 {
  float: right;
  color: #23ac38;
  font-size: 14px;
}

.module-advices-main-comments-contents-c-content {
  margin-top: 16px;
  font-size: 14px;
  color: #272a2f;
}

</style>



