import moduleForm from './components/module-form'
import moduleAdvices from './components/module-advices'
import backToTop from './components/back-to-top'

export default {
  name: 'jyzy-main',
  components: {
    jyzxHeader,
    moduleForm,
    moduleAdvices,
    backToTop
  }
}
</script>

<style lang="scss">
@import "../../styles/variable.scss";

$module1Width: 44%;
$module2Width: 52%;
$marginLeft: 1%;

body {
  background-color: #f5f6f8;
}

.modules {
  padding: 1%;
  position: relative;
  font-family: $base-font-family;
}

.module-form {
  float: left;
  width: $module1Width;
}

.module-advices {
  width: $module2Width;
  margin-left: $marginLeft;
  float: left;
}

</style>



