<template lang="html">
  <div class="back-to-top" @click="onBackToTopClick">
    <img :src="imgBackToTop"/>
  </div>
</template>

<script>
import imgBackToTop from '@assets/imgs/back_to_top.png'
export default {
  data () {
    return {
      imgBackToTop
    }
  },
  methods: {
    onBackToTopClick () {
      window.scrollTo(0, 0)
    }
  }
}
</script>

<style lang="scss">
@import "../../../styles/mixin.scss";

.back-to-top {
  position: fixed;
  right: 0.6%;
  bottom: 1%;
  width: 2.6%;
  @include pointer;
  & img {
    width: 100%;
  }
}
</style>



