<template lang="html">
  <section class="module-form">
    <div class="module-form-feeling">
      <header class="module-form-title">
        <div class="module-form-title-text">
          <span>今日心情</span>
        </div>
      </header>
      <div class="class="module-form-feeling-content>
        <ul class="feelings clearfix">
          <li v-for="feeling in feelings">
            <img :src="feeling.src"/>
            <span>{{feeling.text}}</span>
          </li>
        </ul>
      </div>
    </div>
    <div class="module-form-box">
      <header class="module-form-title">
        <div class="module-form-title-text">
          <span>我有话说</span>
        </div>
      </header>
      <div class="module-form-box-content">
        <div class="module-form-box-to">
          <div class="module-form-box-to-d1">
            <span>至：</span>
          </div>
          <div class="module-form-box-to-d2">
            <span v-for="job in jobs" @click="onToJobClick(job.id)" :class="{ 'active-job': job.id === selectedToJobId }">
              {{job.name}}
            </span>
          </div>
        </div>
        <div class="module-form-box-title">
          <input placeholder="添加标题" v-model.lazy="title"/>
        </div>
        <div class="module-form-box-main">
          <textarea placeholder="有话想说......" v-model.lazy="content"/>
        </div>
      </div>
      <div class="module-form-button">
        <button>提交</button>
      </div>
    </div>
  </section>
</template>

<script>
import imgAngry from '@assets/imgs/feelings/angry.png'
import imgLaugh from '@assets/imgs/feelings/laugh.png'
import imgSad from '@assets/imgs/feelings/sad.png'
import imgShocked from '@assets/imgs/feelings/shocked.png'
import imgSmile from '@assets/imgs/feelings/smile.png'
import { jobs } from '@service/analog-data'

export default {
  data () {
    return {
      selectedFeelingId: null,
      selectedToJobId: null,
      title: '',
      content: ''
    }
  },
  methods: {
    onFeelingClick (id) {
      this.selectedFeelingId = id
    },
    onToJobClick (id) {
      this.selectedToJobId = id
    }
  },
  computed: {
    feelings () {
      const fs = []
      fs.push({
        id: 1,
        src: imgLaugh,
        text: '开心'
      })
      fs.push({
        id: 2,
        src: imgSmile,
        text: '满意'
      })
      fs.push({
        id: 3,
        src: imgShocked,
        text: '一般'
      })
      fs.push({
        id: 4,
        src: imgAngry,
        text: '生气'
      })
      fs.push({
        id: 5,
        src: imgSad,
        text: '委屈'
      })
      return fs
    },
    jobs () {
      return jobs
    }
  }
}
</script>

<style lang="scss">
@import '../../../styles/variable.scss';
@import '../../../styles/mixin.scss';

$fz: 18px;
$input_textarea_radius: 8px;
$input_textarea_border: 1px solid #dfdfdf;
$Input_textarea_padding: 12px;

.module-form {
  @include module-block;
}

.module-form-title {
  margin-bottom: 22px;
}

.module-form-feeling {
  padding-bottom: 20px;
  border-bottom: 1px solid #dfdfdf;
}

.module-form-title-text {
  font-size: 20px;
  font-family: "Microsoft yahei";
  font-weight: 700;
  color: #5f5f5f;
}

.feelings {
  text-align: center;
  color: #9b9b9b;
  & li {
    float: left;
    display: flex;
    flex-direction: column;
    margin-left: 26px;
    @include pointer;
  }
  & li:first-child {
    margin-left: 0;
  }
  & img {
    width: 100%;
    margin-bottom: 4px;
  }
}

.module-form-box {
  margin-top: 20px;
}

.module-form-box-content {

}

.module-form-box-to {
  display: flex;
  $bkg-color: #c9c9c9;
  align-items: center;

  @at-root {
    $marginBottom: 4px;
    .active-job {
      background-color: $bkg-color;
    }

    .module-form-box-to-d1 {
        padding-bottom: $marginBottom;
      & span {
      }
    }

    .module-form-box-to-d2 {
      & span {
        display: inline-block;
        padding: 4px 16px;
        border: 1px solid #c9c9c9;
        border-radius: 20px;
        margin-left: 8px;
        margin-bottom: $marginBottom;
        @include pointer;
      }
      & span:hover {
        background-color: $bkg-color;
      }
    }
  }
}



.module-form-box {
  & input, & textarea {
    width: 100%;
    border: $input_textarea_border;
    padding: $Input_textarea_padding;
    font-size: $fz;
    border-radius: $input_textarea_radius;
    outline: none;
  }
}

.module-form-box-title {

  margin-top: 18px;
  & input {
  }
}

.module-form-box-main {
  margin-top: 12px;
  & textarea {
    resize: none;
    height: 244px;
  }
}

.module-form-button {
  margin-top: 20px;
  & button {
    padding: 10px 84px;
    color: #fff;
    font-family: "Microsoft yahei";
    background-color: #1cba5b;
    border: none;
    font-size: 18px;
    outline: none;
    @include pointer;
  }
}
</style>



